import Link from "next/link";

interface PaginationProps {
    items: number;
    pageSize: number;
    currentPage: number;
    pathName: string;
    previousUrl: string | null;
    nextUrl: string | null;
    pageCount: number;
}

export default function PaginationComponent({ props }: { props: PaginationProps }) {
    const pages = Array.from({ length: props.pageCount }, (_, i) => i + 1);
    return (
        <div className="col-span-3 flex flex-row justify-center gap-2">
            {
                props.previousUrl && (
                    <Link aria-disabled="true" key={'previous'} scroll={false} aria-label="Previous Page" className={`font-dm-sans font-bold border-2 border-partial px-2 rounded-sm`}
                        href={{ pathname: props.pathName, query: { page: (props.currentPage - 1) } }}>
                        Previous
                    </Link>
                )
            }
            {
                pages.length >= 2 && pages.map((page) => (
                    <Link key={page} scroll={false} className={`font-dm-sans font-bold border-2 border-partial px-2 rounded-sm ${page == props.currentPage ? 'bg-primary text-white' : 'bg-[#FFFBF2] text-primary'}`}
                        href={{ pathname: props.pathName, query: { page: page } }}>
                        {page}
                    </Link>
                ))
            }
            {
                props.nextUrl && (
                    <Link key={'next'} scroll={false} aria-label="Next Page" className={`font-dm-sans font-bold border-2 border-partial px-2 rounded-sm`}
                        href={{ pathname: props.pathName, query: { page: (props.currentPage + 1) } }}>
                        Next
                    </Link>
                )
            }
        </div>
    )
}
